<template>
  <div class="datepicker-container">
    <h1>주 단위 달력</h1>
    
    <!-- 주 단위 달력 -->
    <div class="week-picker">
      <button @click="prevWeek">이전 주</button>
      <span>{{ startOfWeek }} - {{ endOfWeek }}</span>
      <button @click="nextWeek">다음 주</button>
    </div>
    
    <div class="week-days">
      <div 
        v-for="day in weekDays" 
        :key="day.date" 
        class="day-cell" 
        :class="{ selected: selectedDate === day.date }"
        @click="selectDate(day.date)"
      >
        <span>{{ day.date }}</span>
        <span>{{ day.day }}</span>
      </div>
    </div>

    <p v-if="selectedDate">선택된 날짜: {{ selectedDate }}</p>

    <!-- 월 선택 버튼 -->
    <div class="month-picker">
      <h2>하단버튼으로 날짜 선택 시 해당 월로 이동</h2>
      <Datepicker 
        v-model="selectedMonth"
        :type="'month'"
        :locale="locale"
        @update:model-value="handleMonthChange"
      />
    </div>
  </div>
</template>

<script>
import { ref, reactive  } from 'vue';
import Datepicker from 'vue3-datepicker';
import { ko } from 'date-fns/locale';
import dayjs from 'dayjs';

const locale = reactive(ko); // 한글 달력 (기본 값은 영어)

export default {
  name: 'DatePickerView',
  components: {
    Datepicker
  },
  setup() {
    const selectedDate = ref(null);
    const currentWeek = ref(dayjs());
    const startOfWeek = ref(currentWeek.value.startOf('week').format('YYYY-MM-DD'));
    const endOfWeek = ref(currentWeek.value.endOf('week').format('YYYY-MM-DD'));
    const weekDays = ref(generateWeekDays(currentWeek.value));
    const selectedMonth = ref(null);

    function generateWeekDays(date) {
      const start = dayjs(date).startOf('week');
      return Array.from({ length: 7 }, (_, i) => {
        return {
          date: start.add(i, 'day').format('YYYY-MM-DD'),
          day: start.add(i, 'day').format('ddd')
        };
      });
    }

    const prevWeek = () => {
      currentWeek.value = currentWeek.value.subtract(1, 'week');
      updateWeek();
    };

    const nextWeek = () => {
      currentWeek.value = currentWeek.value.add(1, 'week');
      updateWeek();
    };

    const selectDate = (date) => {
      selectedDate.value = date;
    };

    const updateWeek = () => {
      startOfWeek.value = currentWeek.value.startOf('week').format('YYYY-MM-DD');
      endOfWeek.value = currentWeek.value.endOf('week').format('YYYY-MM-DD');
      weekDays.value = generateWeekDays(currentWeek.value);
    };

    const handleMonthChange = (value) => {
      // 선택된 월의 첫 번째 주로 이동
      currentWeek.value = dayjs(value).startOf('month');
      updateWeek();
      selectDate(null); // 월을 변경할 때 선택된 날짜 초기화
    };

    return {
      startOfWeek,
      endOfWeek,
      weekDays,
      prevWeek,
      nextWeek,
      selectedDate,
      selectDate,
      selectedMonth,
      handleMonthChange,
      locale
    };
  }
};
</script>

<style>
.datepicker-container {
  text-align: center;
  padding: 20px;
}

.week-picker {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.week-picker button {
  margin: 0 10px;
  padding: 5px 10px;
  cursor: pointer;
}

.week-days {
  display: flex;
  justify-content: center;
}

.day-cell {
  margin: 0 10px;
  padding: 10px;
  border: 1px solid #ccc;
  width: 80px;
  text-align: center;
  cursor: pointer;
}

.day-cell.selected {
  background-color: #b9424c;
  color: white;
}

.month-picker {
  margin-top: 20px;
}
</style>